<template>
	<div class="dibuBox-item" @click="tiaozhuan">
		<div v-if="isS"><slot name="item-img"></slot></div>
		<div v-else><slot name="item-img_select"></slot></div>
		<div><slot name="item-title"></slot></div>
	</div>
	
</template>

<script>
	export default{
		props:{
			 path:{
				 type:String,				 
			 }
		},
		data(){
			return{
				// isShow:true
			}
		},
		created() {
		
		},
		methods:{
			tiaozhuan(){
				this.$router.push(this.path).catch(err=>err)
			}
		},
	    computed:{
		  isS(){
			  return this.$route.path.indexOf(this.path)==-1
		  }
		}
	}
</script>

<style lang="less" scoped>
	.dibuBox-item {
		width: 45px;
		height: 71px;
		font-size: 14px;
		color: #CDD3DB;
		text-align: center;

		img {
			width: 45px;
			height: 44px;
			vertical-align: middle;
		}
	}
</style>
